.align-center {
  text-align: center;
}

.Button, .Link,
.Cell .Tappable,
.Search__icon {
  cursor: pointer;
}

.scroll-to-top-button {
  display: inline-flex;
  border: none;
  outline: none;
  border-radius: 50%;
  padding: 0;
  color: var(--accent);
  width: 48px;
  height: 48px;
  justify-content: center;
  align-items: center;
  background-color: var(--header_background);
  box-shadow: 0px 2px 5px rgba(0,0,0,.08);
  font-weight: bold;
  position: absolute;
  right: 0;
  bottom: 0;
  margin-right: 18px;
  margin-bottom: 24px;
  animation: slowUp .3s;
}

.Panel--ios .scroll-to-top-button {
  box-shadow: 0px 0px 0px 1px rgba(0,0,0,.08);
  width: 42px;
  height: 42px;
}

@keyframes slowUp {
  0% {
    transform: translateY(12px);
    opacity: 0;
  }

  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}

.Header {
  user-select: none;
}

.btn-15 .Button__content {
  font-size: 15px;
}