.cards-wrapper {
  display: flex;
  flex-wrap: wrap;
}

.content-page {
  margin-top: 8px;
  margin-bottom: 82px;
}

.content-page.search {
  margin-top: 64px;
}

.card {
  flex: 1;
  overflow: hidden;
  float: normal;
  width: auto;
  margin-left: 0px !important;
  animation: slideUp .3s;
  margin-bottom: 8px;
  height: 100%;
}

.slide-wrap {
  min-width: 100%;
  width: 100%;
  text-align: center;
}


.description {
  color: var(--text_secondary);
  font-size: 14px;
}

.card--description {
  margin-top: 8px;
  margin-bottom: 4px;
}

.card--checkbox {
  position: absolute;
  top: 0;
  z-index: 2;
  right: 0;
  left: 0;
}


.Tappable--ios.Tappable--active.Tappable.card--checkbox:not([disabled]):not(.TabsItem):not(.PanelHeaderButton):not(.Button):not(.PanelHeaderContent__in):not(.ActionSheetItem),
.Tappable--android.Tappable--active.card--checkbox:not([disabled]):not(.TabsItem):not(.PanelHeaderButton):not(.Button):not(.PanelHeaderContent__in){
  background: transparent !important;
  transition: none;
}

.card--checkbox .Tappable__waves {
  display: none;
}


.button-wrap {
  margin-top: 8px;
}

.bottom-list-actions {
  background: var(--header_alternate_background);
}

.Panel--android .bottom-list-actions {
  box-shadow: 0 -2px 4px 0 rgba(0, 0, 0, .06), 0 0 2px 0 rgba(0, 0, 0, .08);
  background: var(--header_alternate_background);
}

.Panel--ios .bottom-list-actions::before {
  position: absolute;
  bottom: 100%;
  left: 0;
  width: 100%;
  height: 1px;
  background: var(--separator_common);
  transform-origin: center bottom;
  content: '';
}

.bottom-list-actions--items {
  display: flex;
}

.primary-action {
  flex: 4;
  padding-left: 0px;
}

.primary-action .Button {
  height: 100%;
}

.flickity-enabled:focus {
    outline: none;
}


@keyframes slideUp {
  0% {
    transform: translateY(12px);
    opacity: 0;
  }

  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}


.card:first-child {
  margin-right: 8px;
}

.card:last-child {
  margin-left: 8px;
  margin-right: 0px;
}


.card .Card__in {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100%;
}


.CardGrid::after {
  display: none;
}

.card--footer {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: flex-end;
}

.card--footer-title {
  flex: 4;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 400px) {
  .card {
    min-width: 100%;
  }

  .card:first-child {
    margin-right: 0px;
  }

  .card:last-child {
    margin-left: 0px;
    margin-right: 0px;
  }
}